<template>
    <div class="mainBox" :style="{width:1100/scale+ 'px',height:1100/scale+ 'px'}">
        <div id="container" style="height: 100%"></div>
    </div>
</template>

<script>
    export default {
        name: "testechatrs",
        data(){
            return {
                res: [],
                scale: 1.3,
                minQ: 0,
                maxQ: 0
            }
        },
        methods:{
            drawLine(){
                option = null
                let myData = this.res.data
                let minRight = myData[0][2];
                let maxRight = myData[myData.length - 1][2]

                console.log(minRight, maxRight);
                this.minQ = Math.round(minRight);
                this.maxQ = Math.round(maxRight);
                let myChart = this.$echarts.init(document.getElementById('container'))
                var option = {
                    title: {
                        text: 'Breakout',
                        left: 'center',
                        top: 0
                    },
                    visualMap: {
                        min: this.minQ,
                        max: this.maxQ,
                        dimension: 2,
                        orient: 'vertical',
                        right: 0,
                        bottom: '5%',
                        text: ['HIGH', 'LOW'],
                        calculable: true,
                        inRange: {
                            color: ["#00fef6", "#6ab92c", "#fef804", "#ff7b00", "#db253e", "#5b1875"]
                        }
                    },
                    tooltip: {
                        trigger: 'item',
                        axisPointer: {
                            type: 'cross'
                        },
                        alwaysShowContent: true,
                        triggerOn: 'click'
                    },
                    xAxis: [{
                        type: 'value'
                    }],
                    yAxis: [{
                        type: 'value',
                    }],
                    series: [{
                        type: 'scatter',
                        animation: false,
                        symbolSize: 6,
                        // itemStyle: {
                        //     normal: {
                        //         borderWidth: 0.2,
                        //         borderColor: '#fff'
                        //     }
                        // },
                        data: myData,
                        tooltip: {
                            formatter: function (params) {
                                let myStateSrc = './stateImg/state-'+(params.data[3]-1)+ '.png'
                                let myNextStateSrc = './nextStateImg/nextState-'+(params.data[3]-1)+ '.png'
                                return [
                                    'Q value: ' + params.data[2] + '<br/>',
                                    '<p><img style="width:250px" src=' + myStateSrc + '></p>',
                                    '<p><img style="width:250px" src=' + myNextStateSrc + '></p>',
                                ].join('');
                            },
                            position: [900, 50]
                        },

                    }]
                };

                myChart.setOption(option);
            }
        },
        mounted() {
            this.$axios.get("./data_json.json").then((res) => {
                this.res = res.data;
                this.drawLine()
            })
        }
    }
</script>

<style scoped>
    .mainBox {
        margin: auto;
    }
</style>